
[class*="is-mobile"] {
  display: none !important;
}
@media screen and (min-width: 768px) {
.no-sidebar .main {
    padding-bottom: 50px;
    border: 1px solid rgba(0,0,0,.07);
    border-top: none;
    border-radius: 0 0 4px 4px;
    margin-bottom: 40px;
}
}
@media screen and (max-width: 768px) {
.is-desktop {
    display: none !important;
}
.is-mobile {
    display: block !important;
}
.is-mobile-flex {
    display: -webkit-flex !important;
    display: -ms-flexbox !important;
    display: flex !important;
}
.page {
    left: 0;
}
.sidebar {
    width: calc(100% - 50px);
    padding-top: 50px;
    padding-bottom: 10px;
    top: 0;
    border-right: none;
    box-shadow: 0 0 10px rgba(0,0,0,0.2);
    -webkit-transform: translateX(-120%);
        -ms-transform: translateX(-120%);
            transform: translateX(-120%);
    transition: -webkit-transform .3s cubic-bezier(0.4, 0, 0, 1);
    transition: transform .3s cubic-bezier(0.4, 0, 0, 1);
    transition: transform .3s cubic-bezier(0.4, 0, 0, 1), -webkit-transform .3s cubic-bezier(0.4, 0, 0, 1);
    /* Enable scroll with momentum on iOS devices */
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
}
.sidebar.visible {
    -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
            transform: translateX(0);
}
}



