
.sidebar-headings {
  list-style: none;
  margin: 0;
  margin-top: 10px;
  padding-left: 0;
}
.sidebar-headings .sidebar-heading {
  line-height: 1.4;
}
.sidebar-headings .sidebar-heading:not(.visible) {
  display: none;
}
.sidebar-headings .sidebar-heading[data-level="2"] {
  font-weight: bold;
}
.sidebar-headings .sidebar-heading[data-level="2"] .sidebar-heading-anchor {
  color: #333;
}
.sidebar-headings .sidebar-heading[data-level="4"] {
  font-size: 13px;
}
.sidebar-headings .sidebar-heading[data-level="4"] .sidebar-heading-anchor {
  padding-left: 35px;
}
.sidebar-headings .sidebar-heading[data-level="5"] {
  font-size: 13px;
}
.sidebar-headings .sidebar-heading[data-level="5"] .sidebar-heading-anchor {
  padding-left: 50px;
}
.sidebar-headings .sidebar-heading[data-level="6"] {
  font-size: 12px;
}
.sidebar-headings .sidebar-heading[data-level="6"] .sidebar-heading-anchor {
  padding-left: 66px;
}
.sidebar-headings .sidebar-heading .sidebar-heading-anchor {
  color: #666;
  padding: 7px 20px;
  padding-left: 17px;
  display: block;
}
.sidebar-headings .sidebar-heading .sidebar-heading-anchor.active, .sidebar-headings .sidebar-heading .sidebar-heading-anchor:hover {
  color: #42b983;
}
@media screen and (max-width: 768px) {
.sidebar-headings {
    border-top: 1px solid #e2e2e2;
    padding-top: 10px;
}
.sidebar-heading-anchor {
    padding: 7px 10px;
}
}



