
.search-result .empty-search-result {
    padding-top: 10px;
    padding-bottom: 10px;
    color: #8a8a8a;
}
.search-result .result-item {
    cursor: pointer;
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(0,0,0,.07);
}
.search-result .result-item:last-child {
    margin-bottom: 20px;
}
.search-result .result-item:hover {
    background-color: #f9f9f9;
}
.search-result .result-item.active {
    border-left: 3px solid #42b983;
}
.search-result .result-item .result-title {
    color: #42b983;
}
.search-result .result-item .result-content {
    margin-top: 5px;
    color: #666;
}
.search-result .empty-search-result {
    padding-top: 10px;
    color: #666;
}



