
.search-form {
  position: relative;
  border-bottom: 1px solid rgba(0,0,0,.07);
  height: 40px;
}
.search-form.focus .svg-icon {
  color: #333;
}
.search-form .svg-icon {
  cursor: pointer;
  top: 0;
  height: 40px;
  width: 40px;
  position: absolute;
  right: 10px;
  color: rgba(0,0,0,.22);
}
.search-form .svg-icon svg {
  width: 14px;
  height: 14px;
}
.search-form .svg-icon.close:hover {
  color: #f66;
}
.search-form .svg-icon.do-search:hover {
  color: #42b983;
}
.search-box {
  height: 100%;
  border: none;
  width: 100%;
  outline: none;
  font-size: 14px;
}



