
.sidebar-toggle {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 260px;
  height: 50px;
  z-index: 7000;
  background-color: rgba(252, 252, 252, 0.83);
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.sidebar-toggle .toggle-trigger {
  color: #666;
  cursor: pointer;
}
.sidebar-toggle .toggle-trigger:hover {
  color: #333;
}
.sidebar-toggle .toggle-trigger svg {
  width: 22px;
}
.no-sidebar .sidebar-toggle {
  background-color: transparent;
  width: auto;
}
@media screen and (max-width: 768px) {
.sidebar-toggle {
    display: none;
}
}



